@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.home {
  padding: 2em;
  height: 20vh;
}

.home h1 {
  font-size: 60px;
}

.about {
  padding: 2em;
  width: 100vw;
  height: 80vh;
  background-color: white;
}

#aboutAccordion {
  margin-top: 10%;
  width: 70%;
}

#headshot img {
  width: 80%;
}

.skills {
  padding: 2em;
  min-height: 20vh;
  background-color: white;
}

.skills img {
  background-color: white;
  margin-top: 1em;
  height: 100px;
}

.contact {
  padding: 2em;
  min-height: 30vh;
  background-color: lightgray;
}

img {
  margin-bottom: 20px;
}

li, p {
  text-align: left;
}

.projects {
  padding: 2em;
  min-height: 80vh;
  background-color: #246;
  width: 100%;
}

.projects h2 {
  margin-bottom: 1.6em;
  color: white;
}

.card-body:hover {
  cursor: pointer;
}

#details {
  text-align: left;
}

#projectDisplay {
  margin: 2em;
  margin-left: 1em;
  margin-bottom: 20px;
  vertical-align: top;
}

#displayTitle {
  font-size: 34px;
  margin: 0.5em;
}

* {
  text-align: center;
  transition: 0.5s;
  font-family: 'Roboto', sans-serif;
}

